<template>
  <div class="map1" id="viewDiv1">
    <div
      id="line-button"
      class="esri-widget esri-widget--button esri-interactive"
      title="Draw polyline"
    >
      <span class="esri-icon-polyline"></span>
    </div>
  </div>
</template>

<script>
import { setDefaultOptions, loadModules } from "esri-loader";
export default {
  name: "map1",
  data() {
    return {};
  },
  mounted() {
    this.drawmap();
    console.log("process.env.NODE_ENV",process.env.NODE_ENV)
  },
  methods: {
    drawmap() {
      // const options = {
      //   url: `http://localhost/4.15/init.js`,
      //   css: "http://localhost/4.15/esri/css/main.css",
      // };
      // setDefaultOptions({
      //   url: `http://localhost/4.15/init.js`,
      //   css: "http://localhost/4.15/esri/css/main.css",
      // });
       setDefaultOptions({
        url: `https://js.arcgis.com/4.18/`,
        css: "https://js.arcgis.com/4.18/esri/themes/light/main.css",
      });
      loadModules([
        "esri/Map",
        "esri/views/MapView",
        "esri/layers/FeatureLayer",
        "esri/widgets/Legend",
        "esri/Basemap",
        "esri/layers/TileLayer",
        "esri/widgets/Fullscreen",
        "esri/Graphic",
        "esri/views/draw/Draw",
        "esri/geometry/geometryEngine",
      ])
        .then(
          ([
            Map,
            MapView,
            FeatureLayer,
            Legend,
            Basemap,
            TileLayer,
            Fullscreen,
            Graphic,
            Draw,
            geometryEngine,
          ]) => {
            //视图分离
            var basemap = new Basemap({
              baseLayers: [
                new TileLayer({
                  url:
                    "http://map.geoq.cn/arcgis/rest/services/ChinaOnlineStreetPurplishBlue/MapServer",
                  title: "Basemap",
                }),
              ],
              title: "basemap",
              id: "basemap",
            });

            var map = new Map({
              basemap: basemap,
            });

            var view = new MapView({
              container: "viewDiv1",
              map: map,
              zoom: 4, //放大倍数
              center: [108.868851, 34.466148],
            });
            view.ui.components = [];
            const layer0 = new FeatureLayer({
              // URL to the service
              portalItem: {
                id: "eb54b44c65b846cca12914b87b315169",
              },
            });
            layer0.renderer = {
              type: "simple", // autocasts as new SimpleRenderer()
              symbol: {
                type: "picture-marker", // autocasts as new SimpleMarkerSymbol()
                // path: "M14.5,29 23.5,0 14.5,9 5.5,0z",
                url: require("@/assets/mapimg/colored-pins-blue.png"), //"./colored-pins-blue.png",
                width: "20px",
                heigt: "41px",
              },
            };
            map.add(layer0);
            const legend = new Legend({
              view: view,
              container: "legendDiv",
            });
            view.ui.add(legend, "bottom-right");
            const fullscreen = new Fullscreen({
              view: view,
            });
            view.ui.add(fullscreen, "top-right");
            /*************************
             * Create a point graphic
             *************************/

            // First create a point geometry (this is the location of the Titanic)
            var point = {
              type: "point", // autocasts as new Point()
              longitude: 108.868851,
              latitude: 34.466148,
            };

            // Create a symbol for drawing the point
            var markerSymbol = {
              type: "simple-marker", // autocasts as new SimpleMarkerSymbol()
              color: [226, 119, 40],
              outline: {
                // autocasts as new SimpleLineSymbol()
                color: [255, 255, 255],
                width: 2,
              },
            };

            // Create a graphic and add the geometry and symbol to it
            var pointGraphic = new Graphic({
              geometry: point,
              symbol: markerSymbol,
            });

            /****************************
             * Create a polyline graphic
             ****************************/

            // First create a line geometry (this is the Keystone pipeline)
            var polyline = {
              type: "polyline", // autocasts as new Polyline()
              paths: [
                [116.302509, 39.963839],
                [108.980385, 34.465195],
                [121.527341, 31.299022],
              ],
            };

            // Create a symbol for drawing the line
            var lineSymbol = {
              type: "simple-line", // autocasts as SimpleLineSymbol()
              color: [226, 119, 40],
              width: 4,
            };

            // Create an object for storing attributes related to the line
            var lineAtt = {
              Name: "Keystone Pipeline",
              Owner: "TransCanada",
              Length: "3,456 km",
            };

            /*******************************************
             * Create a new graphic and add the geometry,
             * symbol, and attributes to it. You may also
             * add a simple PopupTemplate to the graphic.
             * This allows users to view the graphic's
             * attributes when it is clicked.
             ******************************************/
            var polylineGraphic = new Graphic({
              geometry: polyline,
              symbol: lineSymbol,
              attributes: lineAtt,
              popupTemplate: {
                // autocasts as new PopupTemplate()
                title: "{Name}",
                content: [
                  {
                    type: "fields",
                    fieldInfos: [
                      {
                        fieldName: "Name",
                      },
                      {
                        fieldName: "Owner",
                      },
                      {
                        fieldName: "Length",
                      },
                    ],
                  },
                ],
              },
            });

            /***************************
             * Create a polygon graphic
             ***************************/

            // Create a polygon geometry
            var polygon = {
              type: "polygon", // autocasts as new Polygon()
              rings: [
                [112.512666, 37.982146],
                [106.478352, 29.705967],
                [114.315601, 22.298426],
                [117.148784, 36.689432],
              ],
            };

            // Create a symbol for rendering the graphic
            var fillSymbol = {
              type: "simple-fill", // autocasts as new SimpleFillSymbol()
              color: [227, 139, 79, 0.8],
              outline: {
                // autocasts as new SimpleLineSymbol()
                color: [255, 255, 255],
                width: 1,
              },
            };

            // Add the geometry and symbol to a new graphic
            var polygonGraphic = new Graphic({
              geometry: polygon,
              symbol: fillSymbol,
            });

            // Add the graphics to the view's graphics layer
            view.graphics.addMany([
              pointGraphic,
              polylineGraphic,
              polygonGraphic,
            ]);

            view.ui.add("line-button", "top-left");

            const draw = new Draw({
              view: view,
            });
            // draw polyline button
            document.getElementById("line-button").onclick = function () {
              view.graphics.removeAll();

              // creates and returns an instance of PolyLineDrawAction
              const action = draw.create("polyline");

              // focus the view to activate keyboard shortcuts for sketching
              view.focus();

              // listen polylineDrawAction events to give immediate visual feedback
              // to users as the line is being drawn on the view.
              action.on(
                [
                  "vertex-add",
                  "vertex-remove",
                  "cursor-update",
                  "redo",
                  "undo",
                  "draw-complete",
                ],
                updateVertices
              );
            };

            // Checks if the last vertex is making the line intersect itself.
            function updateVertices(event) {
              // create a polyline from returned vertices
              if (event.vertices.length > 1) {
                const result = createGraphic(event);

                // if the last vertex is making the line intersects itself,
                // prevent the events from firing
                if (result.selfIntersects) {
                  event.preventDefault();
                }
              }
            }

            // create a new graphic presenting the polyline that is being drawn on the view
            function createGraphic(event) {
              const vertices = event.vertices;
              view.graphics.removeAll();

              // a graphic representing the polyline that is being drawn
              const graphic = new Graphic({
                geometry: {
                  type: "polyline",
                  paths: vertices,
                  spatialReference: view.spatialReference,
                },
                symbol: {
                  type: "simple-line", // autocasts as new SimpleFillSymbol
                  color: "red",
                  width: 4,
                  cap: "round",
                  join: "round",
                },
              });

              // check if the polyline intersects itself.
              const intersectingSegment = getIntersectingSegment(
                graphic.geometry
              );

              // Add a new graphic for the intersecting segment.
              if (intersectingSegment) {
                view.graphics.addMany([graphic, intersectingSegment]);
              }
              // Just add the graphic representing the polyline if no intersection
              else {
                view.graphics.add(graphic);
              }

              // return intersectingSegment
              return {
                selfIntersects: intersectingSegment,
              };
            }

            // function that checks if the line intersects itself
            function isSelfIntersecting(polyline) {
              if (polyline.paths[0].length < 3) {
                return false;
              }
              const line = polyline.clone();

              //get the last segment from the polyline that is being drawn
              const lastSegment = getLastSegment(polyline);
              line.removePoint(0, line.paths[0].length - 1);

              // returns true if the line intersects itself, false otherwise
              return geometryEngine.crosses(lastSegment, line);
            }

            // Checks if the line intersects itself. If yes, change the last
            // segment's symbol giving a visual feedback to the user.
            function getIntersectingSegment(polyline) {
              if (isSelfIntersecting(polyline)) {
                return new Graphic({
                  geometry: getLastSegment(polyline),
                  symbol: {
                    type: "simple-line", // autocasts as new SimpleLineSymbol
                    style: "short-dot",
                    width: 3.5,
                    color: "yellow",
                  },
                });
              }
              return null;
            }

            // Get the last segment of the polyline that is being drawn
            function getLastSegment(polyline) {
              const line = polyline.clone();
              const lastXYPoint = line.removePoint(0, line.paths[0].length - 1);
              const existingLineFinalPoint = line.getPoint(
                0,
                line.paths[0].length - 1
              );

              return {
                type: "polyline",
                spatialReference: view.spatialReference,
                hasZ: false,
                paths: [
                  [
                    [existingLineFinalPoint.x, existingLineFinalPoint.y],
                    [lastXYPoint.x, lastXYPoint.y],
                  ],
                ],
              };
            }
          }
        )
        .catch((err) => {
          // handle any errors
          console.error(err);
        });
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
